.Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
}
.Icon-container--checked {
    position: absolute;
    left: 12px;
}
.Icon-container--delete {
    position: absolute;
    top: -24px;
    right: 0;
}
.Icon-png {
    width: 24px;
    height: 24px;
    transition: fill .15s;
}
.Icon-container:hover .Icon-png--checked {
    fill: #4caf50
}
.Icon-container:hover .Icon-png--delete{
    fill: red;
}